@import 'variables-site/colors';
@import 'mixins/mixins-master';

.wp-block-pullquote:not(.has-background),
.pullquote {
  blockquote {
    font-size: 150%;
    border-left: 0px solid transparent;
    border-top: 4px solid $color__link;
    border-bottom: 4px solid $color__link;
    padding-bottom: 1.5em;
  }
}

.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;

	img[class*="wp-image-"] {
		@include center-block;
	}

	.wp-caption-text {
		margin: 0.8075em 0;
	}
}

.wp-caption-text {
	text-align: center;
}

